.StickyExample
{
	height: 25em;
	overflow-y: auto;
	position: relative;
}

.Sticky
{
	width: 100%;
	height: 4rem;
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	z-index: 10;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	background-color: var(--red);
	color: var(--white);
}

.StickyExample .Content
{
	padding-top: 4rem;
	position: absolute;
	top: 0;
}



