.Snackbar
{
	width: 100%;
	margin: 0;
	padding: 1em;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 99;
	text-align: center;
	background-color: var(--red);
	color: var(--white);
	-webkit-transform: translateY(-100%);
	    -ms-transform: translateY(-100%);
	        transform: translateY(-100%);
	opacity: 0;
	-webkit-transition: opacity .2s ease, background-color .3s ease, -webkit-transform .2s ease;
	transition: opacity .2s ease, background-color .3s ease, -webkit-transform .2s ease;
	-o-transition: opacity .2s ease, transform .2s ease, background-color .3s ease;
	transition: opacity .2s ease, transform .2s ease, background-color .3s ease;
	transition: opacity .2s ease, transform .2s ease, background-color .3s ease, -webkit-transform .2s ease;
	cursor: pointer;
}

.Snackbar:hover
{
	background-color: var(--gray-dark);
}

.Snackbar.is-active
{
	-webkit-transform: translateY(0);
	    -ms-transform: translateY(0);
	        transform: translateY(0);
	opacity: 1;
}



